@import '/src/assets/css/styles';

.products-page {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding-top: 24px;

  &__breadcrumbs {
    margin-bottom: 40px;

    @include on-mobile {
      margin-bottom: 14px;
    }
  }

  &__title {
    display: flex;
    flex-direction: column;
    gap: 8px;
    margin-bottom: 40px;
  }

  &__cards {
    display: flex;
    flex-wrap: wrap;
    gap: 16px;
    margin-top: 24px;
    margin-bottom: 40px;

    @include on-small-mobile {
      flex-direction: column;
      gap: 10px;
    }

    @include on-large-mobile {
      column-gap: 2%;
    }

    @include on-tablet {
      column-gap: 2%;
    }

    @include on-laptop {
      column-gap: 2%;
    }
  }
}
